<template>
  <div v-if="redObj" class="redData_box">
    <div
      class="redData_amount"
    >{{$t('Remain')}} {{redObj.rest_number}}/{{redObj.total_number}}, {{redObj.rest_quantity.split(" ")[0]}}/{{redObj.total_quantity}}</div>
    <div class="redData_list_box">
      <template v-if="redObj.logs.length == 0">
        <div class="redData_no_data">{{$t('No data')}}</div>
      </template>
      <template v-else>
        <div v-for="(item,index) of redObj.logs" :key="index" class="redData_list_item">
          <div>{{item.user}}</div>
          <div>{{item.quantity}}</div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    redObj: Object
  },
  data() {
    return {};
  },
  created() {

  },

  methods: {
    goBack() {
      window.history.go(-1);
    }
  },
  computed: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
.redData_box {
  width: 100%;
  margin-bottom: 20 * @rem;
  .redData_amount {
    height: 50 * @rem;
    line-height: 50 * @rem;
    padding: 0 16 * @rem;
    text-align: left;
    color: #a69987;
    font-size: 14 * @rem;
    border-bottom: 1 * @rem solid #f9f9f9;
  }
  .redData_list_box {
    .redData_no_data {
      height: 50 * @rem;
      line-height: 50 * @rem;
      text-align: center;
      font-size: 12 * @rem;
      color: #a69987;
    }
    .redData_list_item {
      padding: 0 16 * @rem;
      color: #5d4220;
      font-size: 14 * @rem;
      display: flex;
      height: 50 * @rem;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1 * @rem solid #f9f9f9;
    }
  }
}
</style>



